import { useState, useRef } from "react";

export const useChatbotLogic = () => {
  // --- Optional loading messages (can rotate if needed) ---
  const loadingMessages = [
    "Thinking...",
    "Analyzing input...",
    "Generating response...",
    "Working on it...",
    "Almost done..."
  ];

  // --- UI State ---
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [showUploadMenu, setShowUploadMenu] = useState(false);
  const [showClearConfirm, setShowClearConfirm] = useState(false);
  const [isEscalateOpen, setIsEscalateOpen] = useState(false);

  // --- Input & Upload ---
  const [inputMessage, setInputMessage] = useState("");
  const [selectedFile, setSelectedFile] = useState(null);
  const [uploadedFiles, setUploadedFiles] = useState([]);
  const fileInputRef = useRef(null);
  const uploadMenuRef = useRef(null);
  const textareaRef = useRef(null);

  // --- Message & Chat ---
  const [isLoading, setIsLoading] = useState(false);
  const [loadingText, setLoadingText] = useState(loadingMessages[0]);
  const [conversations, setConversations] = useState([
    { id: 1, title: "New Conversation" },
  ]);
  const [activeConversationId, setActiveConversationId] = useState(1);
  const [conversationMessages, setConversationMessages] = useState({
    1: [],
  });
  const history = conversationMessages[activeConversationId] || [];
  const [isLoadingMap, setIsLoadingMap] = useState({});
  const messagesEndRef = useRef(null);

  // --- Feedback ---
  const [feedback, setFeedback] = useState({});
  const [feedbackComments, setFeedbackComments] = useState({});
  const [showCommentBox, setShowCommentBox] = useState({});
  const [feedbackSubmitted, setFeedbackSubmitted] = useState({});

  // --- Escalation ---
  const [escalateData, setEscalateData] = useState({
    to: "xx@clr.com",
    cc: "",
    subject: "Manual Escalation From App",
    body: "",
  });

  // --- User ---
  const [currentUser, setCurrentUser] = useState(null);
  const initialized = useRef(false);

  // --- Toasts ---
  const [toastType, setToastType] = useState("success");
  const [toastMessage, setToastMessage] = useState(null);
  const [toastVisible, setToastVisible] = useState(false);

  // -- default react titles -- 
  const defaultLabels = {
    clearChatLabel: "Clear Chat",
    escalateLabel: "Escalate"
  };

  const [labels, setLabels] = useState(defaultLabels);
  

  return {
    // UI
    sidebarOpen, setSidebarOpen,
    showUploadMenu, setShowUploadMenu,
    showClearConfirm, setShowClearConfirm,
    isEscalateOpen, setIsEscalateOpen,

    // Input & Upload
    inputMessage, setInputMessage,
    selectedFile, setSelectedFile,
    uploadedFiles, setUploadedFiles,
    fileInputRef,
    uploadMenuRef,
    textareaRef,

    // Message & Chat
    isLoading, setIsLoading,
    loadingText, setLoadingText,
    conversations, setConversations,
    activeConversationId, setActiveConversationId,
    conversationMessages, setConversationMessages,
    history,
    isLoadingMap, setIsLoadingMap,
    messagesEndRef,

    // Feedback
    feedback, setFeedback,
    feedbackComments, setFeedbackComments,
    showCommentBox, setShowCommentBox,
    feedbackSubmitted, setFeedbackSubmitted,

    // Escalation
    escalateData, setEscalateData,

    // User
    currentUser, setCurrentUser,
    initialized,

    // Toasts
    toastType, setToastType,
    toastMessage, setToastMessage,
    toastVisible, setToastVisible,

    //titles 
    labels, setLabels,
  };
};
