export const sendEscalation = async (escalateData) => {
    // console.log("Sending escalation request with data:", escalateData);
  const res = await fetch("/api/escalate", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(escalateData),
  });

  if (!res.ok) throw new Error(`Escalation failed: ${res.status}`);
  return res.json();
};
